<template>
    <div id="app">
        <MyHeader
            :pages="pages"
            :pageIndex="pageIndex"
            :set_page_index="set_page"
        />
        <MyIndex class="top-gap" v-show="pageIndex == 0" />

        <TagLinkContainer
            class="top-gap"
            v-show="pageIndex == 1"
            :tag="'vue'"
            :tags="tags"
            :links="links"
        />

        <div v-show="pageIndex == 2" class="top-gap">
            <select @change="set_tag" ref="c1">
                <option value="">全部</option>
                <option v-for="(item, index) in Object.keys(tags)" :key="index">
                    {{ item }}
                </option>
            </select>
            <TagLinkContainer :tag="tag" :links="links" :tags="tags" />
        </div>
    </div>
</template>

<script>
import MyHeader from "@/components/MyHeader";
import TagLinkContainer from "@/components/TagLinkContainer";
import MyIndex from "@/components/MyIndex";

export default {
    name: "App",
    data() {
        return {
            labelSel: sessionStorage.getItem("labelSel") || "首页",
            pages: null,
            pageIndex: 0,
            tag: "",
            links: null,
            tags: null,
        };
    },
    components: {
        MyHeader,
        TagLinkContainer,
        MyIndex,
    },
    created() {
        // 载入page信息
        // this.pages = this.readTestFile("page.json");
        this.pages = require("../public/page.json");

        // // 载入page信息里的数据链接
        // this.pages.forEach((element) => {
        //     if (element.dataLink)
        //         element.data = this.readTestFile(element.dataLink);
        // });

        // 分析数据
        // 所有链接
        this.pages[2].data = require("../public/link.json")
        this.links = this.pages[2].data;
        // 所有tag
        let temp = [];
        this.links.forEach((e) => {
            let ts = e.tags;
            ts.forEach((e) => {
                if (temp.indexOf(e) == -1) temp.push(e);
            });
        });
        // 为不同的tag设置一种颜色
        // let cs = this.readTestFile("color.json");
        let cs = require("../public/color.json");

        let i = 0;
        this.tags = {};
        temp.forEach((e) => {
            this.tags[e] = cs[i];
            i = (i + 1) % cs.length;
        });
    },

    mounted() {
        // 从本地缓存中加载数据
        // page index
        let temp = localStorage.getItem("pageIndex");
        this.pageIndex = temp ? +temp : 0;
        // tag
        temp = localStorage.getItem("tag");
        this.tag = temp ? temp : "";
        // 修改select 保持一致
        this.$refs["c1"].value = this.tag;
    },

    methods: {
        open_label(label) {
            this.labelSel = label;
            sessionStorage.setItem("labelSel", label);
        },
        set_page(val) {
            this.pageIndex = val;
            localStorage.setItem("pageIndex", val);
            console.log("设置当前页", val);
        },
        set_tag(event) {
            let val = event.target.value;
            this.tag = val;
            localStorage.setItem("tag", val);
            console.log("设置tag", val);
        },

        // ------------ 这里出现跨域问题，等待解决 ------------
        // 读取test.properties
        readTestFile(path) {
            const file = this.loadFile(path);
            var str = this.unicodeToUtf8(file);
            var obj = eval("(" + str + ")");
            return obj;
        },
        // 读取文件
        loadFile(name) {
            const xhr = new XMLHttpRequest();
            const okStatus = document.location.protocol === "file:" ? 0 : 200;
            xhr.open("GET", name, false);
            xhr.overrideMimeType("text/html;charset=utf-8"); // 默认为utf-8
            xhr.send(null);
            return xhr.status === okStatus ? xhr.responseText : null;
        },
        // unicode转utf-8
        unicodeToUtf8(data) {
            data = data.replace(/\\/g, "%");
            return unescape(data);
        },
        // ------------ END ------------
    },
};
</script>
<style>
#app {
    text-align: center;
}
html {
    background: rgb(243, 242, 242);
}

select,
option {
    width: 100px;
    height: 30px;
    font-size: 20px;
}

.top-gap {
    margin-top: 20px;
}
</style>