<template>
    <div class="header">
        <div
            class="option"
            v-for="(item, index) in pages"
            :key="index"
            @click="set_page_index(index)"
            :class="pageIndex == index ? 'T' : 'F'"
        >
            <div>
                {{ item.name }}
            </div>
            <div class="ctrl">
                {{ pageIndex == index ? "T" : "F" }}
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "MyHeader",
    props: ["pages", "pageIndex", "set_page_index"],
};
</script>

<style scoped>
.header {
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.option {
    width: 100px;
    cursor: pointer;
}

.option.T {
    color: red;
}

.option.T > .ctrl {
    text-decoration: underline;
}
</style>