<template>
    <div>
        <LinkContainer :links="tagLinks" :tags="tags" />
    </div>
</template>

<script>
import LinkContainer from "@/components/LinkContainer";

export default {
    name: "TagLinkContainer",
    props: ["tag", "links", "set_tag", "tags"],
    components: { LinkContainer },
    computed: {
        tagLinks() {
            if (this.tag) {
                var a = [];
                this.links.forEach((e) => {
                    if (e.tags.indexOf(this.tag) != -1) a.push(e);
                });
                return a;
            } else {
                return this.links;
            }
        },
    },
};
</script>

