<template>
    <div class="container">
        <div>
            <div class="row" v-for="(item, index) in links" :key="index">
                <div class="index">
                    {{ index }}
                </div>
                <div class="title">
                    <a :href="item.href">
                        {{ item.title | lengthLimit }}
                    </a>
                </div>
                <div class="tags">
                    <div v-for="(t, i) in item.tags" :key="i">
                        [
                        <div :style="{ display:'inline',color: tags[t] }">{{ t }}</div>
                        ]
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "LinkContainer",

    props: ["links", "tags"],

    filters: {
        lengthLimit(val) {
            if (val.length > 12) return val.slice(0, 12) + "...";
            return val;
        },
    },
};
</script>

<style scoped>
.container {
    display: flex;
    justify-content: center;
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 800px;
    height: 40px;
}

.index {
    width: 40px;
    display: flex;
    justify-content: right;
}

.title {
    width: 300px;
    display: flex;
}
.tags {
    display: flex;
    width: 200px;
}
</style>